<template>
    <div class="bg1-color" style="height:600px;">
        <el-header>
            <el-menu  id="main1-menu"  class="el-menu-demo" mode="horizontal"
                active-text-color="#E6A23C">
                <router-link to="/">
                    <el-col :span="3" class="text2-color">
                        <h1>问卷中心</h1>                  
                    </el-col>       
                </router-link>           
                <div style="float:right;padding:20px 50px;">
                    <router-link to="/register">
                        <el-button round>注册</el-button>
                    </router-link>
                    <router-link to="/login">
                        <el-button  round>登录</el-button>
                    </router-link>                                       
                </div>
            </el-menu>
        </el-header>
        <div style="width:100%;height:50px;"></div>
        <!-- <div class="surveylist-title">问卷列表</div> -->
        <el-card id="contain">
            <!-- 搜索问卷表单 -->
            <div id="search-div">
                <el-form id="searchForm" ref="searchForm" :model="searchForm">
                    <!-- 搜索输入框和搜索按钮 -->
                    <el-row style="width:100%;margin:0 auto;text-align:center;">
                        <el-col :span="12" :offset="6">
                            <el-form-item>
                                <el-input type="text" v-model="searchForm.keyword" placeholder="输入关键字搜索问卷" ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="2">
                            <el-form-item>
                                <el-button type="warning" @click="toSearch()" icon="el-icon-search" circle></el-button>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </div>
            <!-- 问卷搜索结果 -->
            <div id="survey-res">
                <el-table 
                    :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)"
                    border style="width:100%;" 
                    :header-cell-style="{background:'#E6A23C',color:'#fff'}"> 
                    <el-table-column prop="stitle"  align="center" label="问卷标题"></el-table-column>
                    <el-table-column width="130px" prop="num" label="参与热度" align="center"></el-table-column>
                    <el-table-column width="130px"  label="操作" align="center">
                        <template slot-scope="scope">
                            <el-button size="mini" @click="toSurvey(scope.$index, scope.row)" type="info" round>进入</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <!-- 分页操作 -->
            <div id="pages-ctrl">
                <el-pagination
                    class="col-pags"
                    background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page.sync="currentPage"
                    layout="total, prev, pager, next"
                    :total="this.tableData.length">
                </el-pagination>
            </div>
        </el-card>
    </div>
</template>

<style>
    body{
        height: 100% !important;
    }
    body,html{
        background-color:rgb(245, 245, 245) !important;
    }
    .bg1-color{
        background-color:rgb(245, 245, 245);
        width: 100%;
        height: 100%;
        position:absolute;
    }
    #main1-menu {
        background-color: rgba(255, 255, 255, 0);
        border-bottom: none;
        height: 70px;
    }
    .text2-color{
        color: #E6A23C;
        text-align: center;
        line-height: 61px;
        font-size: 12px;
    }
    #survey-res {
        margin: 0 auto;

        width: 784px;
        height: 260px;
        /* border: 1px solid red; */
    }
    #pages-ctrl {
        position: relative;
        margin-left: 638px;
        margin-top: 10px;

        width: 250px;
        height: 45px;

        /* border: 1px solid green; */

    }
    .col-pags.is-background .el-pager li:not(.disabled).active {
        background-color: #E6A23C; 
    }
    .surveylist-title{
        margin: 0 auto;
        padding-bottom:20px;

        width: 980px;

        font-size: 24px;
        font-weight: 300;        
        text-align: center;
    }
    #contain{
        margin: 0 auto;

        width: 980px;
        height: 450px;

        background-color:rgb(245, 245, 245);
        text-align: center;
        /* border: 1px solid red; */
        /* border-radius: 25px; */
    }
    #search-div{
        margin: 15px 0;

        /* border: 1px solid red; */
    }
</style>

<script>
export default {
    data() {
        return {
            searchForm: {
                keyword: ''
            },
            total: 0, //表格数据的总条数
            currentPage:1, // 当前页
            pageSize: 4 , // 每页显示的数据大小
            tableData: []
        }
    },
    methods: {
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.currentPage = val;
        },
        //跳转至问卷详情页
        toSurvey(index,row){   
            console.log(index,row)         
            this.$router.push({
                path: "/survey",
                query: {
                    sid: row.sid
                }
            });
        },
        //加载所有状态为公开的问卷列表
        loadSurveyRes(){
            this.axios.post('api/surveysInfo')
            .then(res => {
                console.log(res.data);
                if (res.status === 200) {
                    this.tableData = res.data.tableData;
                }
            })
            .catch(err => {
                console.error(err); 
            })
        },
        //搜索问卷
        toSearch(){
            console.log(this.searchForm.keyword);
            if (this.searchForm.keyword!="" && this.searchForm.keyword!=null) {
                this.axios.post('/api/search',this.searchForm.keyword)
                .then(res => {
                    console.log(res.data);
                    if (res.status === 200) {
                        this.tableData = res.data.res;
                        this.handleCurrentChange(1);
                    }
                })
                .catch(err => {
                    console.error(err); 
                })
            }else{
                this.loadSurveyRes();
                this.handleCurrentChange(1);
            }
                
        }
    },
    mounted(){
        this.loadSurveyRes();   
        document.title = "问卷库";   
    }
}
</script>





