<template>
    <div style="background-color:rgb(245, 245, 245);height:565px;">
        <div style="width:100%;height:60px;"></div>
        <div id="main-div1">
            <el-row id="main-row1">
                <h1 >我的账号信息</h1>
            </el-row>
            <div id="info-div">
                <el-row>
                    <!-- 用户名模块 -->
                    <el-col :span="12">
                        <el-card>
                            <span class="card-span">我的用户名</span><br>
                            <span class="info-span">
                                {{name}} &nbsp;&nbsp;
                                <el-button type="text" @click="setNameFormDefault()" >修改</el-button>
                                <!-- 修改name的表单dialog -->
                                <el-dialog
                                    title="修改我的用户名"
                                    :visible.sync="editNameShow"
                                    width="25%"
                                    >
                                    <div>
                                        <el-form :model="nameForm">
                                            <el-form-item prop="uname" label="新用户名"  label-width="80px" >
                                                <el-input id="form-name" class="dialog-input" v-model.trim="nameForm.uname" placeholder="请输入您要修改的用户名">
                                                </el-input>
                                            </el-form-item>
                                        </el-form> 
                                    </div>
                                    <span slot="footer" class="dialog-footer">
                                        <el-button @click="editNameShow=false">取消</el-button>
                                        <el-button type="warning" @click="handleEditName()">确定</el-button>
                                    </span>
                                </el-dialog>
                            </span>
                        </el-card>
                        
                    </el-col>
                    <!-- 手机号模块 -->
                    <el-col :span="12" :offset="1">
                        <el-card >
                            <span class="card-span">我的手机号</span><br>
                            <span class="info-span">
                                {{phone}} &nbsp;&nbsp;
                                <el-button type="text" @click="setPhoneFormDefault()" >修改</el-button>
                                <!-- 修改手机号的表单dialog -->
                                <el-dialog
                                    title="修改我的手机号"
                                    :visible.sync="editPhoneShow"
                                    width="25%">
                                    <div>
                                        <el-form :model="phoneForm">
                                            <el-form-item prop="telphone" label="新手机号" label-width="80px" >
                                                <el-input  v-model.trim="phoneForm.telphone" placeholder="请输入您要修改的手机号">
                                                </el-input>
                                            </el-form-item>
                                        </el-form> 
                                    </div>
                                    <span slot="footer" class="dialog-footer">
                                        <el-button @click="editPhoneShow=false">取消</el-button>
                                        <el-button type="warning" @click="handleEditPhone()">确定</el-button>
                                    </span>
                                </el-dialog>
                            </span>
                        </el-card>
                    </el-col>
                </el-row>
                <div style="width:100%;height:20px"></div>
                <el-row>
                    <!-- 性别模块 -->
                    <el-col :span="12">
                        <el-card >
                            <span class="card-span">我的性别</span><br>
                            <span class="info-span">
                                <span v-if="this.sex===1 || this.sex==='1'">男</span>
                                <span v-if="this.sex===0 || this.sex==='0'">女</span>
                                &nbsp;&nbsp;
                                <el-button type="text" @click="setSexFormDefault()" >修改</el-button>
                                <!-- 修改性别的表单dialog -->
                                <el-dialog
                                    title="修改我的性别"
                                    :visible.sync="editSexShow"
                                    width="25%">
                                    <div>
                                        <el-form :model="sexForm">
                                            <el-form-item prop="sex">
                                                <el-select v-model="sexForm.sex" placeholder="请选择您的性别">
                                                     <el-option
                                                        v-for="item in options"
                                                        :key="item.value"
                                                        :label="item.label"
                                                        :value="item.value">
                                                    </el-option>
                                                </el-select>
                                            </el-form-item>
                                        </el-form> 
                                    </div>
                                    <span slot="footer" class="dialog-footer">
                                        <el-button @click="editSexShow=false">取消</el-button>
                                        <el-button type="warning" @click="handleEditSex()">确定</el-button>
                                    </span>
                                </el-dialog>
                            </span>
                        </el-card>
                    </el-col>
                    <!-- 出生日期模块 -->
                    <el-col :span="12" :offset="1">
                        <el-card >
                            <span class="card-span">我的出生日期</span><br>
                            <span class="info-span">
                                {{birthdate}} &nbsp;&nbsp;
                                <el-button type="text" @click="setDateFormDefault()" >修改</el-button>
                                <!-- 修改出生日期的表单dialog -->
                                <el-dialog
                                    title="修改我的出生日期"
                                    :visible.sync="editBirthdateShow"
                                    width="25%">
                                    <div>
                                        <el-form :model="dateForm">
                                            <el-form-item prop="birthdate">
                                                <el-date-picker
                                                    v-model="dateForm.birthdate"
                                                    type="date"
                                                    placeholder="选择日期"
                                                    format="yyyy 年 MM 月 dd 日"
                                                    value-format="yyyy-MM-dd">
                                                </el-date-picker>
                                            </el-form-item>
                                        </el-form> 
                                    </div>
                                    <span slot="footer" class="dialog-footer">
                                        <el-button @click="editBirthdateShow=false">取消</el-button>
                                        <el-button type="warning" @click="handleEditDate()">确定</el-button>
                                    </span>
                                </el-dialog>
                            </span>
                        </el-card>
                    </el-col>
                </el-row>
            </div>            
        </div>          
    </div>
</template>
    
<style>
    
    #main-div1 {
        margin: 0 auto;

        width:715px;
        height:450px;
        /* border: 1px solid #111; */

    }

    #main-row1 {
        line-height: 76px;

        height: 76px;
        /* border: 1px solid #f0f; */
    }
    #main-row1 h1{
        font-size: 28px;
        font-weight: 300;
    }

    #info-div {
        width: 100%;
        height: 280px;
        /* background-color: #fff; */
        /* border: 1px solid rgb(54, 64, 211); */
    }

    #info-div > .el-row {
        width: 100%;
        height: 50%;

    }

    #info-div > .el-row > .el-col {
        width: 45%;
        height: 100%;
        /* border: 1px solid red; */
    }
    #info-div > .el-row > .el-col > .el-card {
        height: 100%;
    }

    .card-span {
        /* font-family: 'SimSun'; */
        font-size: 18px;
        font-weight: bold;
        color: #E6A23C;

        display: block;
        margin-top: 20px;
        margin-left: 20px;

        /* border: 1px solid black; */
    }

    .info-span {
        /* font-family: 'FangSong';
        font-size: 16px; */
        text-align: center;
        color: gray;

        display: block;
        margin: 0 auto;

        /* border: 1px solid black; */
    }

    
</style>

<script>
export default {
    data() {
        return {
            id: this.$route.query.id,
            name: '',
            phone: '',
            sex: '',
            birthdate: '',

            editNameShow: false,
            editPhoneShow: false,
            editSexShow: false,
            editBirthdateShow: false,

            options: [{
                value: '1',
                label: '男'
            }, {
                value: '0',
                label: '女'
            }],

            nameForm:{
                uname: ''
            },
            phoneForm:{
                telphone:''
            },
            sexForm:{
                sex:''
            },
            dateForm:{
                birthdate:''
            },

            

        };
    },
    methods: {
        // 控制对话框关闭的回调函数
        handleClose(done) {
            // this.$confirm('确认关闭？')
            // .then(_ => {
            //     done();
            // })
            // .catch(_ => {});
        },
        // 设置修改用户名的默认值
        setNameFormDefault(){
            this.nameForm.uname = this.name;
            this.editNameShow=true;
        },        
        // 设置修改手机号的默认值
        setPhoneFormDefault(){
            this.phoneForm.telphone = this.phone;
            this.editPhoneShow=true;
        },
        // 设置修改性别的默认值
        setSexFormDefault(){
            this.sexForm.sex = this.sex.toString();
            this.editSexShow=true;
        },
        // 设置修改出生日期的默认值
        setDateFormDefault(){
            this.dateForm.birthdate = this.birthdate;
            this.editBirthdateShow=true;
        },
        // 判断修改的名字是否有效
        nameVailated(val){
            console.log(val);   
            // 判断uname是否为空或null
            if(val === null || val === ""){
                this.$message({
                    message:"请输入您要修改的用户名",
                    type: "warning"
                })
                return false;
            }
            return true;
        },
        // 判断修改的手机号是否有效
        phoneVailated(val){
            console.log(val);   
            // 判断telphone是否为空或null
            if(val === null || val === ""){
                this.$message({
                    message:"请输入您要修改的手机号",
                    type: "warning"
                })
                return false;
            }else if (val.length != 11) {
                this.$message({
                    message:"手机号格式不正确",
                    type: "warning"
                })
                return false;
            }
            return true;
        },
        // 判断修改的sex值是否有效
        sexVailated(val){
            console.log(val)
            // 判断sex值是否为空或null
            if (val === null || val === "") {
                this.$message({
                    message:"请选择您的性别",
                    type: "warning"
                })
                return false;
            }else if (val>1 || val<0) {
                this.$message.error("出现错误");
                return false;
            }
            return true;            
        },
        // 判断修改的birthdate是否有效
        birthdateVailated(val){
            console.log(val)
            // 判断uname是否为空或null
            if(val === null || val === ""){
                this.$message({
                    message:"选择您的出生日期",
                    type: "warning"
                })
                return false;
            }
            return true;
        },
        // 修改用户名
        handleEditName(){
            var val = this.nameForm.uname;
            if(this.nameVailated(val)){
                console.log("用户名有效")
                this.axios.post("api/setName",{
                    username: val,
                    id: this.id
                })
                .then(res => {
                    console.log(res)
                    if (res.status === 200) {
                        if (res.data.msg === "same"){
                            this.$message({
                                message:"用户名没有修改",
                                type: "warning"
                            })
                            this.editNameShow = true;
                        }else if (res.data.msg === "success") {
                            this.name = res.data.username;
                            this.nameForm.uname = res.data.username;
                            this.$message({
                                message:"用户名修改成功",
                                type: "success"
                            })
                            this.editNameShow = false;
                        }else if(res.data.msg === "error"){
                            this.$message.error("出现错误，请稍后再试");
                            this.editNameShow = false;
                        }else if(res.data.msg === "fail"){
                            this.$message({
                                message:"该用户名不可用",
                                type: "warning"
                            })
                            this.editNameShow = true;
                        }
                    }
                })
                .catch(err => {
                    console.error(err); 
                })
            }else{
                this.editNameShow = true;
            }
        },
        // 修改手机号
        handleEditPhone(){
            var val = this.phoneForm.telphone;
            if (this.phoneVailated(val)) {
                console.log("手机号有效")
                this.axios.post("/api/setTel",{
                    id: this.id,
                    telephone: val
                })
                .then(res => {
                    console.log(res)
                    if (res.status === 200) {
                        if (res.data.msg === "same") {
                            this.$message({
                                message:"手机号没有修改",
                                type: "warning"
                            })
                            this.editPhoneShow = true;
                        }else if (res.data.msg === "success") {
                            this.phone = res.data.phone;
                            this.phoneForm.telphone = res.data.phone;
                            this.$message({
                                message:"手机修改成功",
                                type: "success"
                            })
                            this.editPhoneShow = false;
                        }else if (res.data.msg === "error") {
                            this.$message.error("出现错误，请稍后再试");
                            this.editPhoneShow = false;
                        }else if (res.data.msg === "fail") {
                            this.$message({
                                message:"该手机号已其他账号被绑定",
                                type: "warning"
                            })
                            this.editPhoneShow = true;
                        }
                    }
                })
                .catch(err => {
                    console.error(err); 
                })
            }else{
                this.editPhoneShow = true;
            }
        },
        // 修改性别
        handleEditSex(){
            var val = this.sexForm.sex;
            if (this.sexVailated(val)) {
                console.log("sex值有效")
                this.axios.post("/api/setSex",{
                    id: this.id,
                    sex: val
                })
                .then(res => {
                    console.log(res)
                    if (res.status === 200) {
                        if (res.data.msg === "same") {                            
                            this.$message({
                                message:"没有任何修改",
                                type: "warning"
                            })
                            this.editSexShow = true;
                        }else if (res.data.msg === "success") {
                            this.sex = res.data.sex;
                            this.sexForm.sex = res.data.sex;
                            this.$message({
                                message:"修改成功",
                                type: "success"
                            })
                            this.editSexShow = false;
                        }else if (res.data.msg === "error") {
                            this.$message.error("出现错误，请稍后再试");
                            this.editSexShow = true;
                        }else if (res.data.msg === "fail") {
                            this.$message({
                                message:"sex值无效",
                                type: "warning"
                            })
                            this.editSexShow = true;
                        }
                    }
                })
                .catch(err => {
                    console.error(err); 
                })
                
            }else{
                this.editSexShow = true;
            }
        },
        // 修改出生日期
        handleEditDate(){
            var val = this.dateForm.birthdate;
            if (this.birthdateVailated(val)) {
                console.log("日期有效")
                this.axios.post("/api/setBirthdate",{
                    id: this.id,
                    birthdate: val
                })
                .then(res => {
                    console.log(res)
                    if (res.status === 200) {
                        if (res.data.msg === "same") {                            
                            this.$message({
                                message:"没有任何修改",
                                type: "warning"
                            })
                            this.editBirthdateShow = true;
                        }else if (res.data.msg === "success") {
                            this.birthdate = res.data.birthdate;
                            this.dateForm.birthdate = res.data.birthdate;
                            this.$message({
                                message:"修改成功",
                                type: "success"
                            })
                            this.editBirthdateShow = false;
                        }else if (res.data.msg === "error") {
                            this.$message.error("出现错误，请稍后再试");
                            this.editBirthdateShow = true;
                        }else if (res.data.msg === "fail") {
                            this.$message({
                                message:"date值无效",
                                type: "warning"
                            })
                            this.editBirthdateShow = true;
                        }
                    }

                })
                .catch(err => {
                    console.error(err); 
                })
            }
        },
        // 加载用户信息
        loadUserInfo(){
            this.axios.post("api/userInfo",{id:this.id})
            .then(res => {
                console.log(res)
                if (res.status === 200) {
                    var user = res.data.user;
                    this.name = user.username;
                    this.phone = user.telephone;
                    this.birthdate = user.birthdate;
                    this.sex = user.sex;
                }
            })
            .catch(err => {
                console.error(err); 
            });
        }
    },
    // 钩子函数：页面加载前优先调用
    mounted: function(){
        this.loadUserInfo();
        document.title = "我的用户信息";
    }
};
</script>


