// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

import axios from 'axios'
import ECharts from 'vue-echarts/components/ECharts'
import VueClipboard from 'vue-clipboard2'
import VueAxios from 'vue-axios'
import ElementUI from 'element-ui';
import 'default-passive-events';
import 'element-ui/lib/theme-chalk/index.css';

Vue.config.productionTip = false
Vue.use(ElementUI)

// 引入axios
// 设置反向代理
Vue.use(VueAxios,axios) //全局注册
axios.defaults.baseURL='http://localhost:8181'
axios.defaults.headers["Content-type"] = "application/json;x-www-form-urlencoded;charset=UTF-8"

//引入ECharts
// 注册ECharts为标签
Vue.component('chart', ECharts)
// a配置：按需引入echart图表配置，同下b配置
import '../node_modules/echarts/lib/chart/bar'
// import '../node_modules/echarts/lib/chart/line'
import '../node_modules/echarts/lib/chart/pie'
// import '../node_modules/echarts/lib/chart/tooltip'
// b配置：直接引入所有echart配置,同上a配置
// require('echarts')


//引入VueClipboards
//全局注册
Vue.use(VueClipboard)

// 设置浏览器标题
Vue.directive('title', {
  inserted: function (el, binding) {
    document.title = el.dataset.title
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



