<template>
    <div class="bg-color">
    <el-container id="main">
        <el-header id="main-header">
            <el-menu  id="main-menu"  :default-active="$router.path" router  class="el-menu-demo" mode="horizontal" @select="handleSelect"
                active-text-color="#E6A23C">
                <el-col :span="3" class="text-color"><h1>问卷中心</h1></el-col>
                <el-menu-item index="/">首页</el-menu-item>
                <el-menu-item index="/surveylist">问卷库</el-menu-item>
                <el-menu-item style="float:right;padding:0px 100px;">
                    <router-link to='/login'>
                        <el-button type="warning" round>登录</el-button>
                    </router-link>                    
                </el-menu-item>
            </el-menu>
        </el-header>
        <el-carousel :interval="2000" type="outside" height="607px">
            <el-carousel-item v-for="item in 3" :key="item">
                <h3 @click="toSurvey(item)" v-if="item==1">垃圾分类</h3>
                <h3 @click="toSurvey(item)" v-if="item==2">保护环境</h3>
                <h3 @click="toSurvey(item)" v-if="item==3">运动健康</h3>
            </el-carousel-item>
        </el-carousel>
        <!-- <div style="width:100%;height:50px;"></div>
        <el-container>
            <el-row >
                <el-col :span="5" v-for="o in 3" :key="o" :offset="2">
                    <el-card :body-style="{ padding: '0px' }">
                        <img src="./images/12.jpg" class="image">
                        <div style="padding: 14px;">
                            <span>问卷题目</span>
                            <div class="bottom clearfix">
                            <time class="time">{{ currentDate }}</time>
                                <span>问卷简介问卷简介</span>
                                <el-button type="success" class="button">进入</el-button>
                            </div>
                        </div>
                    </el-card>
                </el-col>
            </el-row>
        </el-container> -->
    </el-container>
    </div>
</template>

<style>
    .bg-color{
        position: absolute;
        background-color:white;
        width: 100%;
        height: 100%;
    }
    #main, #main-header, #main-menu {
        margin: 0;
        padding: 0;
        width: 100%;
    }

    #main-menu {
        background-color: white;
        border-bottom: none;
        height: 70px;
    }

    .text-color{
        color: #E6A23C;
        text-align: center;
        line-height: 61px;
    }

    .el-carousel__item h3 {
        text-align: center;
        color: rgb(219, 154, 154);
        font-size: 72px;
        /* opacity: 0.75; */
        line-height: 550px;
        margin: 0;
    }    
    .el-carousel__item:nth-child(2n) {
        background: url('./images/4.jpeg') no-repeat;
        background-size: 100% 100%;
        /* background-color: #99a9bf; */
    }    
    .el-carousel__item:nth-child(2n-1) {
        background: url('./images/5.jpeg') no-repeat;
        background-size: 100% 100%;
        /* background-color: #99a9bf; */
    }
    .el-carousel__item:last-child {
        background: url('./images/8.jpeg') no-repeat;
        background-size: 100% 100%;
        /* background-color: #99a9bf; */
    }
    /* .time {
        font-size: 13px;
        color: #999;
    } */    
    .bottom {
        margin-top: 13px;
        line-height: 12px;
    }

    .button {
        padding: 0;
        float: right;
    }

    .image {
        width: 100%;
        height: 250px;
        display: block;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }
    
    .clearfix:after {
        clear: both
    }
</style>

<script>
  export default {
    data() {
      return {
        activeIndex: '1',
        activeIndex2: ''
      };
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      toSurvey(index){
          if (index === 1) {
              //垃圾分类
              this.$router.push({
                path: "/survey",
                query: {
                    sid: 4
                }
            });
          }else if (index === 2) {
              //保护环境
              this.$router.push({
                path: "/survey",
                query: {
                    sid: 3
                }
            });
          }else if (index === 3) {
              //运动健康
              this.$router.push({
                path: "/survey",
                query: {
                    sid: 1
                }
            });
          }
      }
    },
    mounted(){
        document.title = "在线问卷调查系统";
    }
  }
</script>


