<template>
	<div class="bg-color">
	<el-container>
		<!-- <div style="width:100%;height:30px;"></div> -->
		<el-header>
				<el-menu  id="main-menu1" :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect"
					active-text-color="#E6A23C">
					<el-col :span="8" class="text-color"><h1>问卷中心</h1></el-col>                
					<el-menu-item style="float:right;padding:0px 50px;">
						<router-link to="/login">
							<el-button round>登录</el-button>
						</router-link>
						<router-link to="/">
							<el-button  round>返回首页</el-button>
						</router-link>                                       
					</el-menu-item>
				</el-menu>
			</el-header>   
			<div style="width:100%;height:20px;"></div>    
			<div id="register-title">注册新的账号</div>
			<el-container>
				<el-row>
					<el-col :span="12">
						<div  style="width:380px;height:474px;margin-left:280px;">
							<el-carousel :interval="3000" arrow="never" type="outside" height="474px">
								<el-carousel-item id="register-img1"></el-carousel-item>
								<el-carousel-item id="register-img2"></el-carousel-item>
							</el-carousel>
						</div>
					</el-col>
					<el-col :span="12">
						<el-form id="register-form" :model="regForm" status-icon :rules="rules" ref="regForm">
							<el-form-item  prop="username">
								<el-input class="input-bgcolor" v-model="regForm.username"  prefix-icon="el-icon-star-on" placeholder="请输入用户名" ></el-input>
							</el-form-item>
							<el-form-item  prop="password">
								<el-input class="input-bgcolor" type="password" v-model="regForm.password"  prefix-icon="el-icon-star-on" autocomplete="off" placeholder="请输入密码" ></el-input>
							</el-form-item>
							<el-form-item  prop="checkPass">
								<el-input class="input-bgcolor" type="password" v-model="regForm.checkPass"  prefix-icon="el-icon-star-on" autocomplete="off" placeholder="请输入确认密码" ></el-input>
							</el-form-item> 
							<el-form-item  prop="telephone">
								<el-input class="input-bgcolor"  prefix-icon="el-icon-star-on" v-model="regForm.telephone" placeholder="请输入手机号"></el-input>
							</el-form-item>
							<el-row>
								<el-col :span="6">
								<el-form-item  prop="sex">
									<el-select style="width:100%;" v-model="regForm.sex" placeholder="请选择您的性别">
										<el-option	v-for="item in options"	
										:key="item.value" :label="item.label"
										:value="item.value">
										</el-option>
									</el-select>
								</el-form-item>
								</el-col>
								<el-col :span="4" :offset="1">
								<el-form-item  prop="birthdate">
									<el-date-picker class="input-bgcolor" 
										v-model="regForm.birthdate"
										type="date"
										format="yyyy 年 MM 月 dd 日"
										value-format="yyyy-MM-dd"
										placeholder="选择您的出生日期"
										:picker-options="pickerOptions">
									</el-date-picker>
								</el-form-item>
								</el-col>              
							</el-row>
							<el-form-item>
								<el-button style="width:100%;" type="warning" @click="submitForm('regForm')" round>注册</el-button>
							</el-form-item>
							<el-form-item>
								<el-button style="width:100%;" @click="resetForm('regForm')" round>重置</el-button>
							</el-form-item>
						</el-form>
					</el-col>
				</el-row>
			
			</el-container>  
			
		
	</el-container>
  </div>
</template>
<style scoped>
    .bg-color{
        background-color:white;
        width: 100%;
        height: 100%;
    }
  /* body {
    background-color:  white;
  } */
  #main-menu1 {
        background-color: rgba(255, 255, 255, 0);
        border-bottom: none;
        height: 70px;
    }
    .text-color{
        color: #E6A23C;
        text-align: center;
        line-height: 61px;
    }
    #register-title {      
        /* margin-right: 250px; */
        padding-bottom:20px;
        padding-right: 250px;
        font-size: 28px;
        font-weight: 300;
        text-align: right;
        /* background-color: #fff; */
        /* border: 1px solid #111; */
    }
    #register-form {
        /* margin: 0 auto; */
        padding: 30px 30px 10px 30px;
        /* margin-right: 300px; */
		    margin-left: 155px;
        font-weight: 300;
        background-color: rgb(236, 234, 234);
        width: 320px;
        line-height: 30px;
    }

    #register-img1 {
        background: url('./images/54.jpg') no-repeat;
        background-size: 100% 100%;
        /* background-color: #99a9bf; */
        /* border: 1px solid #111; */
    }    
    #register-img2 {
        background: url('./images/55.jpg') no-repeat;
        background-size: 100% 100%;
        /* background-color: #99a9bf; */
    }
    .el-input__icon::before, .el-icon-star-on::before{
      color: red;
    }
  
</style>
<script>
  export default {
    data() {
      var checkUsername = (rule, value, callback) => {
        if (value === '') {
          return callback(new Error('请输入用户名'));
        }else{
          callback();
        }
      }
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        }
        else if (value.length<6 || value.length>12) {
          callback(new Error('密码长度在6至12个字符内'));
        }
        else {
          if (this.regForm.checkPass !== '') {
            this.$refs.regForm.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.regForm.password) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      var checkTelephone = (rule, value, callback) => {
        if (value === '') {
          return callback(new Error('请输入手机号'));
        }else if (value.length != 11) {
          return callback(new Error('请输入正确的手机号'));
        }else{
          callback();
        }
      }
      return {
        regForm: {
          username: '',
          password: '',
          checkPass: '',
          sex: '1',
          telephone: '',
          birthdate: '2020-01-01'
        },
          options: [{
            value: '1',
            label: '男'
          }, {
            value: '0',
            label: '女'
          }],
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          }
        },
        rules: {
          username: [{required: true, validator: checkUsername, trigger: 'blur' }],
          password: [{required: true, validator: validatePass, trigger: 'blur' }],
          checkPass: [{required: true, validator: validatePass2, trigger: 'blur' }],
          telephone: [{required: true, validator: checkTelephone, trigger: 'blur'}]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
			  this.axios.post("/api/register",JSON.stringify(this.regForm))
			  .then(res => {
				  if(res.status === 200){
					  if (res.data.msg === "unusable") {
						  this.$message.error("用户名已占用，请重新输入。");
					  }else if(res.data.msg === "fail"){
						  this.$message("系统异常，注册失败。");
					  }else if(res.data.msg === "success"){
						  this.regSuccess();
					  }
				  }
			  })
			  .catch(err => {
				//   console.error(err);
				this.$message.error(err); 
			  })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      regSuccess(){
        this.$message({
          message: "注册成功，正在前往登录...",
          type: "success"
        });
        
        return new Promise((resolve,reject) => {
          setTimeout(() => {
            this.$router.replace("/login");
          }, 2000);
        })      
      }
    },
    mounted(){
        document.title = "账号注册";
    }
  }
</script>


