<template>
    <div style="background-color:rgb(245, 245, 245);">
        <!-- 回到顶部 -->
        <template>
            <el-backtop :bottom="100"></el-backtop>
        </template>
        <div class="title">
            <h1>{{stitle}}</h1>
        </div>
        <el-row><h4 style="color:#E6A23C;width:784px;margin:0 auto;text-align:right;">总提交数：{{num}}</h4></el-row>
        <div v-if="num>0">
            <div v-for="(item,index) in res" :key="index" class="infinite-list-item" >
                <div class="temp2"></div>
                <el-card v-if="item.qtype===3"  style="width:784px;height:350px;margin: 0 auto;">
                    <div style="width:700px;margin:0 auto;font-size:20px;color:#E6A23C;">{{index+1}}、{{item.qtitle}}</div>
                    <div class="demo boder" style="text-align:center;">
                        <div class="temp1"></div>
                        <div style="height:210px;">
                            <el-table 
                                :data="item.answerData.slice((item.currentPage-1)*pageSize,item.currentPage*pageSize)"
                                border style="width:100%;" 
                                :header-cell-style="{background:'#E6A23C',color:'#fff'}"> 
                                <el-table-column type="index"></el-table-column>
                                <el-table-column prop="response" label="回答内容" align="center"></el-table-column>
                            </el-table>
                        </div>                            
                        <div class="temp2"></div>
                        <el-pagination 
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"                                
                            :page-size="pageSize"
                            :current-page.sync="item.currentPage"
                            layout="total, prev, pager, next"
                            :total="item.answerData.length">
                        </el-pagination>
                    </div>
                </el-card> 
                <el-card v-else style="width:784px;margin: 0 auto;">
                    <div style="width:700px;margin:0 auto;font-size:20px;color:#E6A23C;">{{index+1}}、{{item.qtitle}}</div>
                    <div :id="'charts'+item.qid" class="demo1 boder">
                        <!--chart标签就是画布元素，设置chart的宽和高可以改变画布大小-->
                        <!--options绑定的数据实际配置项，对应echarts实例中的option后面的对象-->
                        <chart :options="item.option" style="margin:0 auto;"></chart>
                    </div>
                </el-card>
                <div class="temp3"></div> 
            </div>
        </div>
        <div v-if="num===0">
            <el-card style="height:200px;width:784px;margin:0 auto;margin-top:50px;text-align:center;line-height:150px">
                <h2>暂无问卷回答数据</h2>
            </el-card>
        </div>
        <div style="width:100%;height:50px;"></div>
    </div>
</template>

<style>
    body,html{
        background-color: rgb(245, 245, 245);
    }
    .full {
        width: 100%;
        height: 100%;
    }
    .title{
        margin: 0 auto;
        margin-bottom: 30px;
        width: 790px;

        color: #E6A23C;
        text-align: center;
    }
    .title > h1{
        font-size: 28px;
    }
    .temp{
        width: 100%;
        height: 35px;        
    }
    .temp1{
        width: 100%;
        height: 25px;        
    }
    .temp2{
        width: 100%;
        height: 10px;        
    }
    .border{
        border: 1px solid red;
    }
    .demo {
        /* position: relative; */
        margin-left: 100px;
        width: 550px;
        height: 250px;
    }
    .demo1 {
        /* position: relative; */
        /* padding-left: 70px; */
        width: 100%;
        height: 250px;
    }
</style>

<script>
export default {
    data(){
        return {
            uid: this.$route.query.id,
            sid: this.$route.query.sid,
            stitle:'',            
            res:[],
            num:'',
            total: 0, //表格数据的总条数
            pageSize: 3 , // 每页显示的数据大小
        }
    },
    methods: {
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.currentPage = val;
        }
    },
    mounted:function() {
            //获取回答数据，res[],num,stitle
            this.axios.post("api/getResponse",{sid:this.sid})
            .then(res => {
                if (res.status===200) {
                    var data = JSON.parse(JSON.stringify(res.data.res));
                    this.num = res.data.num;
                    this.stitle = res.data.stitle;
                    document.title = "分析—"+this.stitle;
                    console.log(this.res)
                    data.forEach(element => {
                        if (element.qtype===1) {
                            //单选题，饼状图
                            element.option = JSON.parse(JSON.stringify({
                                    title : {
                                        x:'center',//x轴方向对齐方式
                                    },
                                    tooltip : {
                                        trigger: 'item',
                                        formatter: "{a} <br/>{b} : {c} ({d}%)"
                                    },
                                    legend: {
                                        orient: 'vertical',
                                        left: '70%',
                                        y: 'top',
                                        data:element.legend
                                    },
                                    series : [
                                        {
                                            name: '访问来源',
                                            type: 'pie',
                                            radius: '37%',  //图的大小
                                            center: ['35%', '35%'], //图的位置，距离左跟上的位置
                                            data:element.series,
                                            itemStyle: {
                                                emphasis: {
                                                    shadowBlur: 10,
                                                    shadowOffsetX: 0,
                                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                                }
                                            }
                                        }
                                    ]
                            }));
                        }else if(element.qtype===2){
                            //多选题，柱状图
                            element.option = JSON.parse(JSON.stringify({
                                    color: ['#3398DB'],
                                    tooltip: {
                                        trigger: 'axis',
                                        axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                                            type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                                        }
                                    },
                                    grid: {
                                        x:1,
                                        y:40,
                                        width:580,
                                        height:200,
                                        containLabel: true
                                    },
                                    xAxis: [
                                        {
                                            type: 'category',
                                            data: element.legend,
                                            axisTick: {
                                                alignWithLabel: true
                                            }
                                        }
                                    ],
                                    yAxis: [
                                        {
                                            type: 'value'
                                        }
                                    ],
                                    series: [
                                        {
                                            type: 'bar',
                                            barWidth: '30%',
                                            data: element.series
                                        }
                                    ]
                            }));
                        }else{
                            element.currentPage = 1;
                        }
                    });
                    this.res = data;
                    console.log(this.res)
                }
            })
            .catch(err => {
                console.error(err); 
            })
    }
}
</script>


