<template>
    <div style="background-color:rgb(245, 245, 245);height:100%;">
        <el-container id="survey">
            <el-header id="survey-header">
                <el-menu :default-active="$router.path" router id="survey-menu" class="el-menu-demo" mode="horizontal" @select="handleSelect"
                    active-text-color="#E6A23C">
                    <!-- <router-link to="/"> -->
                    <el-col :span="3" class="text-color" style="width:200px;">
                        <h1>问卷中心</h1>
                    </el-col>
                    <!-- </router-link> -->
                    <el-menu-item @click="toSurveyList()">我的调查问卷</el-menu-item>
                    <el-submenu index="#" style="float:right;padding:0px 20px;"> 
                        <template slot="title" >{{name}}</template>
                        <el-menu-item @click="toUserInfo()"><i class="el-icon-user"></i>用户信息</el-menu-item>
                        <el-menu-item @click="toUpdatePwsd()"><i class="el-icon-setting"></i>修改密码</el-menu-item>
                        <el-menu-item index="/"><i class="el-icon-switch-button"></i>退出账号</el-menu-item>
                    </el-submenu> 
                </el-menu>
            </el-header>
            <div style="width:100%;height:40px;"></div>
            <router-view></router-view>
        </el-container>
    </div>
</template>

<style>
    body,html {
        /* background-color: rgba(250, 202, 157, 0.973); */
        /* background-color: #fff; */
        background-color:rgb(245, 245, 245);    
    }
    #survey, #survey-header, #survey-menu {
        margin: 0;
        padding: 0;
        width: 100%;
    }

    #survey-menu {
        border-bottom: none;
    }

    
    .el-pagination.is-background .el-pager li:not(.disabled).active {
        background-color: #E6A23C; 
    }
    .text-color{
        color: #E6A23C;
        text-align: center;
        line-height: 61px;
    }
</style>

<script>
export default {
    data() {
        return {
            // user: JSON.parse(localStorage.getItem("user")),
            activeIndex: '/mysurvey',
            activeIndex2: '',
            id: this.$route.query.id,
            name: ''
        }
    },
    methods: {
        handleSelect(key, keyPath) {
            console.log(key, keyPath);
        },
        toUserInfo(){
            this.$router.push({
                path: "/userinfo",
                query: {
                    id: this.id
                }
            })
        },
        toUpdatePwsd(){
            this.$router.push({
                path: "/updatePwsd",
                query: {
                    id: this.id
                }
            })
        },
        toSurveyList(){
            this.$router.push({
                path: "/mysurvey",
                query: {
                    id: this.id
                }
            })
        }
    },
    mounted:function(){
        this.axios.post("/api/getName",{
            id: this.id
        })
        .then(res => {
            console.log(res)
            if (res.status === 200) {
                this.name = res.data.username;
            }
        })
        .catch(err => {
            console.error(err); 
            this.$message.error(err);
        })
    }

}
</script>


