<template>
    <div style="background-color:rgb(245, 245, 245);height:150%;">
            <!-- <div style="width:100%;height:30px;"></div>         -->
            <div id="main-div">
                <el-row style="width:980px;margin:0 auto;">
                    <el-form id="searchForm" ref="searchForm" :model="searchForm">
                    <el-col :span="6">
                        <div class="survey-search" style="width:95%;">
                            <el-input class="search-input" v-model="searchForm.keyword" placeholder="输入关键字搜索问卷"></el-input>
                        </div>
                    </el-col>
                    <el-col :span="2">
                        <div class="survey-search" style="text-align:left;">
                            <el-button size="small" @click="toSearch()" type="info" icon="el-icon-search" circle></el-button>
                        </div>
                    </el-col>
                    </el-form>
                    <el-col :span="4" :offset="12">
                        <div id="newsurvey-btn">
                            <el-button style="height:70%;width:100%;font-size:16px;" @click="toNewSurvey()" type="warning" round>建立调查问卷</el-button>
                            <!-- 新建问卷的表单dialog -->
                            <el-dialog
                                title="新建调查问卷"
                                :visible.sync="editStitleShow"
                                width="35%">
                                <div>
                                    <el-form label-width="80px" :model="stitleForm" ref="stitleForm" :rules="stitleRule">
                                        <el-form-item label="问卷标题" prop="stitle">
                                            <el-input id="form-stitle" v-model.trim="stitleForm.stitle" placeholder="请输入问卷标题">
                                            </el-input>
                                        </el-form-item>
                                    </el-form> 
                                </div>
                                <span slot="footer" class="dialog-footer">
                                    <el-button @click="editStitleShow=false">取消</el-button>
                                    <el-button type="warning" @click="submitStitle('stitleForm')">新建</el-button>
                                </span>
                            </el-dialog>
                        </div>
                    </el-col>
                </el-row>
                <el-row style="width:980px;margin:0 auto;">
                    <!-- 我的问卷信息表格 -->
                    <div id="survey-list" style="margin:0 auto; height:264px;">
                        <el-table 
                        :data="surveyData.slice((currentPage-1)*pageSize,currentPage*pageSize)"
                        border style="width:100%;" 
                        :header-cell-style="{background:'#E6A23C',color:'#fff'}"
                        :default-sort = "{prop: 'createTime', order: 'descending'}">                             
                            <el-table-column width="265px" prop="stitle" label="标题">
                                <template slot-scope="scope">
                                    <span>{{scope.row.stitle}}</span>
                                    <el-tooltip class="item" effect="dark" content="预览问卷" placement="top-start">
                                        <el-button type="text" style="color:gray;font-size:12px;" icon="el-icon-view" @click="toView(scope.$index,scope.row)"></el-button>                                            
                                    </el-tooltip>
                                </template>
                            </el-table-column>
                            <el-table-column width="87px" prop="response" sortable label="提交数" align="center"></el-table-column>
                            <el-table-column width="105px" prop="createTime" sortable label="建立日期" align="center"></el-table-column>
                            <el-table-column prop="lastTime" label="修改时间" align="center"></el-table-column>
                            <el-table-column width="87px"  label="状态" align="center">
                                <template slot-scope="scope">
                                    <span v-if="scope.row.publish===1">
                                        已发布
                                        <el-tooltip class="item" effect="dark" content="切换问卷状态" placement="top-start">
                                            <el-button type="text"  style="color:gray;font-size:12px;" icon="el-icon-refresh" @click="showEditPublish(scope.row.sid,scope.row.publish)"></el-button>                                            
                                        </el-tooltip>
                                    </span>
                                    <span v-if="scope.row.publish===0">
                                        未发布
                                        <el-tooltip class="item" effect="dark" content="切换问卷状态" placement="top-start">
                                            <el-button type="text" style="color:#666;font-size:12px;" icon="el-icon-refresh" @click="showEditPublish(scope.row.sid,scope.row.publish)"></el-button>                                            
                                        </el-tooltip>                                                                               
                                    </span>
                                    <!-- 切换问卷状态 -->
                                    <el-dialog
                                        title="切换问卷状态"
                                        :visible.sync="editPublishShow"
                                        width="35%">
                                        <div>
                                            <span v-if="currentPublish===1">确定要将该问卷状态设为<i>未发布</i> 吗？</span>
                                            <span v-if="currentPublish===0">确定要将该问卷状态设为<i>已发布</i> 吗？</span>
                                        </div>
                                        <span slot="footer" class="dialog-footer">
                                            <el-button @click="editPublishShow2=false">取消</el-button>
                                            <el-button type="warning" @click="changePublish(currentSid,currentPublish)">切换</el-button>
                                        </span>
                                    </el-dialog>                                    
                                </template>
                            </el-table-column>
                             
                            <el-table-column width="60px"  label="分析" align="center">
                                <template slot-scope="scope">
                                <el-button v-if="scope.row.publish===0" @click="toAnalysis(scope.row.sid)" type="text" style="color:#666;font-size:16px;" icon="el-icon-pie-chart" disabled></el-button>
                                <el-button v-if="scope.row.publish===1" @click="toAnalysis(scope.row.sid)" type="text" style="color:#666;font-size:16px;" icon="el-icon-pie-chart" ></el-button>
                                </template>
                            </el-table-column>
                            <el-table-column width="60px"  label="分享" align="center">
                                <template slot-scope="scope">
                                <el-button v-if="scope.row.publish===0" type="text" style="color:#666;font-size:16px;" icon="el-icon-share" disabled></el-button>
                                <el-button v-if="scope.row.publish===1" type="text" style="color:#666;font-size:16px;" icon="el-icon-share"
                                    v-clipboard:copy="'http://localhost:8181/#/survey?sid='+scope.row.sid"
                                    v-clipboard:success="onCopy" ></el-button>
                                </template>
                            </el-table-column>
                            <el-table-column width="120px"  label="操作" align="center">
                                <template slot-scope="scope">
                                    <el-button v-if="scope.row.publish===0" size="mini" @click="editSurvey(scope.$index,scope.row)" icon="el-icon-edit" circle></el-button>
                                    <el-button v-if="scope.row.publish===0" size="mini" type="danger" @click="deleteSurvey(scope.$index,scope.row)" icon="el-icon-delete" circle></el-button>
                                    
                                    <el-button v-if="scope.row.publish===1" size="mini" @click="editSurvey(scope.$index,scope.row)" icon="el-icon-edit" circle disabled></el-button>
                                    <el-button v-if="scope.row.publish===1" size="mini" type="danger" @click="deleteSurvey(scope.$index,scope.row)" icon="el-icon-delete" circle disabled></el-button>                                    
                                </template>                                
                            </el-table-column>
                        </el-table>
                    </div>
                </el-row>    
                <!-- 表格分页组件 -->
                <el-row style="width:980px;margin:0 auto;">
                    <el-col :span="10" :offset="14">
                        <div id="survey-pages">
                            <el-pagination
                                class="col-pags"
                                background
                                @size-change="handleSizeChange"
                                @current-change="handleCurrentChange"                                
                                :page-size="pageSize"
                                :current-page.sync="currentPage"
                                layout="total, prev, pager, next"
                                :total="this.surveyData.length">
                            </el-pagination>
                            <!-- :current-page.sync="currentPage1" -->
                        </div>
                    </el-col>
                </el-row>             
            </div>
    </div>
</template>

<style>
    body {
        /* background-color: rgba(250, 202, 157, 0.973); */
        background-color: rgb(245, 245, 245);
    }
    #survey, #survey-header, #survey-menu {
        margin: 0;
        padding: 0;
        width: 100%;
    }

    #survey-menu {
        border-bottom: none;
    }

    #main-div{
        margin: 0 auto;
        padding-top: 50px;

        width: 1012px;
        height: 490px;
        /* border: 1px solid #111; */
    }

    .survey-search {
        /* margin: 0 auto; */
        padding: 10px 0;
        line-height: 60px;

        width: 100%;
        height: 60px;
        /* border: 1px solid red; */
    }    

    #newsurvey-btn {
        padding: 10px 0;
        line-height: 60px;
        float: right;

        width: 100%;
        height: 60px;
        /* border: 1px solid red; */
    }

    #survey-list {
        width: 100%;
        /* height: 380px; */
        /* border: 1px solid green; */
    }

    .el-dropdown-link {
        cursor: pointer;
        color: #666;
    }
    .el-dropdown-link:hover,
    .el-icon-pie-chart:hover, 
    .el-icon-share:hover,
    .el-icon-refresh:hover,
    .el-icon-view:hover{
        color: #E6A23C ;
    }
    /* .el-dropdown-menu__item::before {
        color: #E6A23C;
        background-color: rgba(255, 193, 100, 0.788);
    } */
    .el-icon-arrow-down {
        font-size: 12px;
    }

    #survey-pages {
        margin-top: 15px;
        padding: 8px 0;
        /* line-height: 30px; */

        width: 100%;
        height: 30px;
        /* border: 1px solid red; */
        text-align: right;
    }
    .col-pags.is-background .el-pager li:not(.disabled).active {
        background-color: #E6A23C; 
    }
    .text-color{
        color: #E6A23C;
        text-align: center;
        line-height: 61px;
    }
    .confirmButton{
        background-color: #E6A23C !important;
        border-color: #E6A23C !important;
        font-size: 14px;
    }
    .cancelButton{
        font-size: 14px;
    }
</style>

<script>
export default {
    data() {
        var checkStitle = (rule, value, callback) => {
            if (value === '') {
                return callback(new Error('问卷标题不能为空'));
            }else{
                callback();
            }
        };
        return {
            uid: this.$route.query.id,
            // name: this.$route.query.name,
            total: 0, //表格数据的总条数
            currentPage:1, // 当前页
            pageSize: 3 , // 每页显示的数据大小
            surveyData: [],
            currentSid: '',//当前操作问卷的编号
            currentPublish: '',//当前操作问卷的状态
            editStitleShow: false,
            editPublishShow: false,
            searchForm:{
                keyword: ''
            },
            stitleForm:{
                stitle: ''
            },
            stitleRule:{
                stitle:[{required: true, validator: checkStitle, trigger: 'blur' }]
            }
        }
    },
    methods: {        
        handleSelect(key, keyPath) {
            console.log(key, keyPath);
        },
        handleCommand(command) {
            if (command === 'edit') {
                this.$message({
                message:"尚未开发，敬请期待"
                // ,type: 'success'
            });
            }else if (command === 'delete') {
                this.$message({
                message:"正在跳转至问卷分析页...",
                type: 'success'
            });
            }else if (command === 'copylink') {
                this.$message({
                message:"成功复制问卷链接，可以发送给调查对象啦",
                type: 'success'
            });
            }
            
        },
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.currentPage = val;
        },
        //预览按钮
        toView(index,row){
            this.$router.push({
                path: "/view-survey",
                query: {
                    id: this.uid,
                    sid: row.sid
                }
            });
        },
        //分享按钮：复制问卷链接至剪贴板
        onCopy(e){
            console.log(e);
            this.$message({
                type:"success",
                message: "问卷链接已复制到剪贴板，可用分享给小伙伴啦~~"
            });
        },
        //切换按钮
        showEditPublish(sid,publish){
                this.editPublishShow=true;
                this.currentPublish = publish;
                this.currentSid = sid;
        },
        //切换问卷状态
        changePublish(sid,publish){
            this.axios.post("api/setPublish",{sid:sid,publish:publish})
            .then(res => {
                console.log(res)
                if(res.status===200){
                    if (res.data.msg === "success") {
                        this.editPublishShow = false;
                        this.$message({
                            type: "success",
                            message: "问卷状态切换成功。"
                        });
                        this.loadSurveyTable();
                    }else{
                        this.editPublishShow = false;
                        this.$message({
                            type: "warnning",
                            message: "出现错误，稍后再试。"
                        });
                    }
                }
            })
            .catch(err => {
                console.error(err); 
            })  
        },
        //新建问卷信息
        submitStitle(formName){
            this.$refs[formName].validate((valid) => {
                if(valid){
                    // 新建问卷信息
                    this.axios.post('/api/addSurveyInfo',{
                        stitle: this.stitleForm.stitle,
                        uid: this.uid
                    })
                    .then(res => {
                        console.log(res)
                        if (res.status === 200) {
                            if (res.data.msg === 'success') {
                                this.toEditSurvey(res.data.sid);
                            }else{
                                this.$message({
                                    message: "出现错误，请稍后再试。",
                                    type: 'warning'
                                });
                            }
                        }
                    })
                    .catch(err => {
                        console.error(err); 
                    })
                }
            });
        },
        //新建问卷跳转至编辑页
        toEditSurvey(sid){
            this.$message({
                message: "操作成功，正前往问卷编辑页面...",
                type: 'success'
            });
            return new Promise((resolve,reject) => {
			  setTimeout(() => {
                this.$router.push({
                    path: "/new-survey",
                    query: {
                        id: this.uid,
                        sid: sid
                    }
                });
			  }, 2000);
		  })
        },
        //新建问卷按钮
        toNewSurvey(){
            this.editStitleShow = true;
        },
        //搜索按钮
        toSearch(){
            if (this.searchForm.keyword!="" && this.searchForm.keyword!=null) {
                this.axios.post('/api/getSurveyByUid',{
                    uid: this.uid,
                    keyword:this.searchForm.keyword
                })
                .then(res => {
                    console.log(res.data);
                    if (res.status === 200) {
                        this.surveyData = res.data.surveyData;
                        this.currentPage = 1;
                    }
                })
                .catch(err => {
                    console.error(err); 
                })
            }else{
                this.loadSurveyTable();
            }
        },
        //分析按钮
        toAnalysis(sid){
            this.$message({
                message:"正在跳转至问卷分析页...",
                type: 'success'
            });            
            return new Promise((resolve,reject) => {
                setTimeout(() => {
                    this.$router.push({
                        path: "/survey-data",
                        query: {
                            id: this.uid,
                            sid:sid
                        }
                    });
                }, 1000);
            })
        },
        //编辑按钮——修改问卷
        editSurvey(index,row){
            this.$router.push({
                path: "/edit-survey",
                query: {
                    id: this.uid,
                    sid: row.sid
                }
            });
        },
        //删除按钮——删除问卷
        deleteSurvey(index,row){
            this.$confirm('此操作将删除该问卷的所有数据, 是否继续?', '提示', {
            confirmButtonText: '确定',
            confirmButtonClass:'confirmButton',
            cancelButtonText: '取消',
            cancelButtonClass: 'cancelButton',
            type: 'warning'
            }).then(() => {
                //删除问卷
                this.axios.post("api/delSurvey",{sid:row.sid})
                .then(res => {
                    if (res.status===200) {
                        if (res.data.msg==="success") {
                            this.$message({
                                type: 'success',
                                message: '删除成功!'
                            });
                            this.loadSurveyTable();
                        }else{
                            this.$message({
                                type: 'warning',
                                message: '出现错误，稍后再试!'
                            });
                        }
                    }
                })
                .catch(err => {
                    console.error(err); 
                })                            
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
        },
        //加载表格数据
        loadSurveyTable(){
            console.log(this.uid);
            //加载表格
            this.axios.post("/api/mySurveys",{
                id: this.uid
            })
            .then(res => {
                console.log(res.data);
                if (res.status === 200) {
                    this.surveyData =  res.data.mysurveylist;
                    this.currentPage = 1;
                }
            })
            .catch(err => {
                console.error(err); 
            })
        }
    },
    //加载页面的时候，加载表格
    mounted: function(){
        this.loadSurveyTable();
        document.title = "我的问卷列表";
    }
}


</script>


