<template>
    <div class="bg-color">
    <el-container>
        <!-- <div style="width:100%;height:30px;"></div> -->
        <el-header>
            <el-menu  id="main-menu1" :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect"
                active-text-color="#E6A23C">
                    <el-col :span="8" class="text-color"><h1>问卷中心</h1></el-col>                
                    <div style="float:right;padding:18px 50px;">
                        <router-link to="/register">
                            <el-button round>注册</el-button>
                        </router-link>
                        <router-link to="/">
                            <el-button  round>返回首页</el-button>
                        </router-link>                                       
                    </div>
            </el-menu>
        </el-header>
        <div style="width:100%;height:20px;"></div>
        <div id="login-title">登录您的账号</div>
        <el-container>
            <el-row>
                <el-col :span="12">
                    <el-form id="login-form"   ref="loginForm" :model="loginForm" :rules="rules" >                                       
                        <el-form-item  prop="username">
                            <h3>输入您的用户名：</h3> 
                            <el-input v-model="loginForm.username" placeholder="用户名"></el-input>
                        </el-form-item>               
                        <el-form-item  prop="password">
                            <h3>输入您的密码：</h3>
                            <el-input  v-model="loginForm.password" placeholder="密码" show-password></el-input>
                        </el-form-item>
                        <el-form-item>
                            <!-- <el-checkbox type="warning" v-model="remember">记住我</el-checkbox><br> -->
                            <router-link class="reset-link" to="/resetPwsd"><span>忘记密码>></span></router-link>
                        </el-form-item>
                        <el-form-item>
                            <el-button style="width:100%;" type="warning" @click="submitForm('loginForm')" round>登录</el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
                <el-col :span="12">
                    <div style="width:380px;height:506.56px;margin-left:150px;">
                        <el-carousel :interval="3000" arrow="never" type="outside" height="460px">
                            <el-carousel-item id="login-img1"></el-carousel-item>
                            <el-carousel-item id="login-img2"></el-carousel-item>
                        </el-carousel>
                    </div>
                </el-col>
            </el-row>            
        </el-container>
        
    </el-container>
    </div>
</template>

<style>
    .bg-color{
        background-color:white;
        width: 100%;
        height: 100%;
    }
    #main-menu1 {
        background-color: rgba(255, 255, 255, 0);
        border-bottom: none;
        height: 70px;
    }
    .reset-link{
        color: #E6A23C;
        text-decoration: none;
    }
    .reset-link:hover{
        text-decoration: underline;
    }
    .login-photo {
        margin: 0 auto;
        margin-right: 300px;
        width: 350px;
    }
    .form-divider {
        /* border-top: 1.5px solid white; */
        border-bottom: 3px solid white;
    }
    .text-color{
        margin:0px;
        color: #E6A23C;
        text-align: center;
        line-height: 61px;
        width:200px;
    }
    #login-title {
        margin-left: 300px;
        padding-bottom:20px;
        font-size: 28px;
        font-weight: 300;
    }
    #login-form .el-form-item h3{
        font-weight: 500;
    }
    #login-form {
        /* margin: 0 auto; */
        padding: 30px ;
        margin-left: 300px;
        font-weight: 300;
        background-color: rgb(236, 234, 234);
        width: 300px;
        height: 400px;
        line-height: 50px;
    }
    #login-img1 {
        background: url('./images/45.jpg') no-repeat;
        background-size: 100% 100%;
        /* background-color: #99a9bf; */
    }    
    #login-img2 {
        background: url('./images/98.jpg') no-repeat;
        background-size: 100% 100%;
        /* background-color: #99a9bf; */
    }
    

</style>

<script>
  export default {
    data() {
      var checkUsername = (rules,value, callback) => {
        if (value === '') {
            return callback(new Error('请输入用户名'));
        }else{
            callback();
        }
      };
      var checkPass = (rules,value, callback) => {
        if (value === '') {
            return callback(new Error('请输入密码'));
        }else{
            callback();
        }
      };
        return {
            loginForm: {
                username:'',
                password:''                
            },
            // remember: false,
            // responseResult: [],
            rules: {
                username:[
                    {validator:checkUsername, trigger:'blur'}
                ],
                password:[
                    {validator:checkPass, trigger:'blur'}
                ]
            }
        }
    },
    methods: {
        getMessage(){
            this.$message("此功能正在开发，敬请期待...");
        },
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
            if (valid) {
                this.axios
                .post('/api/login',JSON.stringify(this.loginForm))
                .then(res => {
                    if (res.status === 200) {
                        console.log(res.data.name);
                        if (res.data.msg === 'fail') {
                            this.$message.error("用户名或密码错误，请重新输入");
                        }else if (res.data.msg === 'success') {
                            this.loginSuccess(res.data.id,res.data.name);
                        }                    
                    }
                })
                .catch(err => {
                    this.$message(err);
                })
            } else {
                console.log('error submit!!');
                return false;
            }
            });
        },
        loginSuccess(uid,uname){
            
            this.$message({
                    message: "登录成功，正在前往...",
                    type: "success"
            });		  
            return new Promise((resolve,reject) => {
                setTimeout(() => {
                    this.$router.push({
                        path: "/mysurvey",
                        name: "MySurvey",
                        query: {
                            //   name: uname,
                            id: uid
                        }
                    });
                }, 2000);
            })
        }
    },
    mounted(){
        document.title = "用户登录";
    }
  }
</script>


