<template>
    <!-- 问卷编辑页面 -->
    <div style="background-color:rgb(245, 245, 245);">
        <!-- 回到顶部 -->
        <template>
            <el-backtop :bottom="100"></el-backtop>
        </template>
        <!-- 问卷标题 -->
        <div class="survey-title">
            <el-row>
                <el-col :span="8" :offset="8">
                    <h1>{{stitle}}</h1>
                </el-col>
                <el-col  :span="1" >
                    <el-button size="small" @click="setStitleFormDefualt()" icon="el-icon-edit" type="" circle></el-button>
                    <!-- 修改问卷标题的表单dialog -->
                    <el-dialog
                        title="修改问卷标题"
                        :visible.sync="editStitleShow"
                        width="28%">
                        <div>
                            <el-form :model="stitleForm" ref="stitleForm" :rules="stitleRule">
                                <el-form-item prop="stitle">
                                    <el-input id="form-stitle" class="dialog-input" v-model.trim="stitleForm.stitle" placeholder="请输入您要修改的用户名">
                                    </el-input>
                                </el-form-item>
                            </el-form> 
                        </div>
                        <span slot="footer" class="dialog-footer">
                            <el-button @click="editStitleShow=false">取消</el-button>
                            <el-button type="warning" @click="handleEditStitle('stitleForm')">确定</el-button>
                        </span>
                    </el-dialog>
                </el-col>
            </el-row>
        </div>        
        <div id="survey-form">
            <!-- 动态表单 -->
            <el-form :inline="true" label-width="80px" id="surveyForm" ref="surveyForm" :rules="surveyRule" :model="surveyForm">
                <div v-for="(item,index) in surveyForm.question" :key="index" class="surveyform-div">
                    <!-- 单选题 -->
                    <el-card v-if="item.qtype === '1'||item.qtype === 1" class="surveyform-card">
                        <!-- 问题输入框 --> 
                        <el-form-item  label="问题内容" :prop="'question.'+index+'.qtitle'">
                            <el-row  style="width:500px;"  :gutter="10">
                                <el-col :span="18" style="width:350px;">
                                    <!-- {{item.qid}} -->
                                    <el-input type="textarea" v-model="item.qtitle" placeholder="请输入问题内容"></el-input>                                    
                                </el-col>
                                <el-col :span="4" style="color:#E6A23C;">[单选题]</el-col>
                                <el-col :span="2">
                                    <el-button size="mini" @click="deleteQuestion(index,item)" type="info" icon="el-icon-delete" circle></el-button>
                                </el-col>
                            </el-row>                            
                        </el-form-item>
                        <!-- 选项输入框 -->
                        <el-form-item v-for="(op,i) in item.options" :key="i" :label="'选项'+(i+1)" :prop="'question.'+index+'.options.'+i+'.option'">
                            <el-row :gutter="20">
                                <el-col :span="20" style="width:300px;">
                                    <!-- {{op.oid}} {{op.qid}} -->
                                    <el-input type="text"  v-model="op.option" placeholder="请输入选项内容"></el-input>
                                </el-col>
                                <el-col :span="4" style="width:50px" >
                                    <!-- v-else-if="i>1" -->
                                    <el-button size="mini" type="danger" icon="el-icon-minus" @click="deleteOption(item.options,i,op)" circle></el-button>
                                </el-col>
                                <el-col :span="4" style="width:50px" v-if="i===item.options.length-1">
                                    <el-button style="display: inline-block;" size="mini" @click="addOption(item.options)" type="primary" icon="el-icon-plus" circle></el-button>
                                </el-col>
                            </el-row>
                        </el-form-item>
                    </el-card>
                    <!-- 多选题 -->
                    <el-card v-if="item.qtype === '2'||item.qtype === 2" class="surveyform-card">
                        <!-- 问题输入框 --> 
                        <el-form-item  label="问题内容" :prop="'question.'+index+'.qtitle'">
                            <el-row  style="width:500px;"  :gutter="10">
                                <el-col :span="18" style="width:350px;">
                                    <!-- {{item.qid}} -->
                                    <el-input type="textarea" v-model="item.qtitle" placeholder="请输入问题内容"></el-input>                                    
                                </el-col>
                                <el-col :span="4" style="color:#E6A23C;">[多选题]</el-col>
                                <el-col :span="2">
                                    <el-button size="mini" @click="deleteQuestion(index,item)" type="info" icon="el-icon-delete" circle></el-button>
                                </el-col>
                            </el-row>                            
                        </el-form-item>
                        <!-- 选项输入框 -->
                        <el-form-item v-for="(op,i) in item.options" :key="i" :label="'选项'+(i+1)" :prop="'question.'+index+'.options.'+i+'.option'">
                            <el-row :gutter="20">
                                <el-col :span="20" style="width:300px;">
                                    <!-- {{op.oid}} {{op.qid}} -->
                                    <el-input type="text"  v-model="op.option" placeholder="请输入选项内容"></el-input>
                                </el-col>
                                <el-col :span="4" style="width:50px" >
                                    <!-- v-else-if="i>1" -->
                                    <el-button size="mini" type="danger" icon="el-icon-minus" @click="deleteOption(item.options,i,op)" circle></el-button>
                                </el-col>
                                <el-col :span="4" style="width:50px" v-if="i===item.options.length-1">
                                    <el-button style="display: inline-block;" size="mini" @click="addOption(item.options)" type="primary" icon="el-icon-plus" circle></el-button>
                                </el-col>
                            </el-row>
                        </el-form-item>
                    </el-card>
                    <!-- 简答题 -->
                    <el-card v-if="item.qtype === '3'||item.qtype === 3" class="surveyform-card">
                        <!-- 问题输入框 --> 
                        <el-form-item  label="问题内容" :prop="'question.'+index+'.qtitle'">
                            <el-row  style="width:500px;"  :gutter="10">
                                <el-col :span="18" style="width:350px;">
                                    <!-- {{item.qid}} -->
                                    <el-input type="textarea" v-model="item.qtitle" placeholder="请输入问题内容"></el-input>                                    
                                </el-col>
                                <el-col :span="4" style="color:#E6A23C;">[简答题]</el-col>
                                <el-col :span="2">
                                    <el-button size="mini" @click="deleteQuestion(index,item)" type="info" icon="el-icon-delete" circle></el-button>
                                </el-col>
                            </el-row>                            
                        </el-form-item>
                    </el-card>
                </div>
                <!-- 完成按钮 -->
                <div id="save-button">
                    <el-button v-if="surveyForm.question.length >0" type="warning" @click="openFullScreen2" round>完成</el-button>
                </div>
            </el-form>
            <!-- 操作成功对话框 -->
            <el-dialog
                title="发布问卷"
                :visible.sync="editPublishShow"
                width="35%" center>
                <div style="font-size:16px;text-align:center;">                            
                    <span>问卷已成功保存！是否发布该问卷？</span>
                </div>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="handleSuccess(1)">暂不发布</el-button>
                    <el-button type="warning" @click="handleSuccess(0)">发布</el-button>
                </span>
            </el-dialog>
        </div>                
        <!-- 新增问题按钮-固定在底部 -->
        <el-row class="fix-button">
            <div id="add-item-button">
                <el-dropdown @command="handleCommand">
                    <el-button type="warning">
                        <i class="el-icon-circle-plus-outline"></i> 新增问题<i class="el-icon-arrow-down el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item command='one'><i class="el-icon-menu"></i> 单选题</el-dropdown-item>
                        <el-dropdown-item command='more'><i class="el-icon-document-checked"></i> 多选题</el-dropdown-item>
                        <el-dropdown-item command='text'><i class="el-icon-edit-outline"></i> 简答题</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </div>
        </el-row>                    
    </div>
</template>

<style>
    body,html {
        background-color:rgb(245, 245, 245);
    }
    .temp{
        width: 100%;
        height: 75px;        
    }
    .temp1{
        width: 100%;
        height: 25px;        
    }
    .border{
        border: 1px solid red;
    }
    .survey-title{
        margin: 0 auto;
        padding-top: 20px;
        padding-bottom: 30px;

        color: #E6A23C;
        text-align: center;
        font-size: 16px;
    }
    #survey-form{
        margin-bottom: 140px;
    }
    .surveyform-div{
        margin: 0 auto;
        margin-bottom: 10px;
        
        width: 784px;
        /* height: 250px; */
    }
    .surveyform-card{
        margin: 0 auto;
        padding-left: 60px;
        padding-top: 20px;

        width: 82%;
        height: 80%;

        background: rgba(255, 255, 255, 0.5);
    }
    #save-button{
        margin: 0 auto;

        width: 649px;
        height: 80px;
        text-align: right;
        line-height: 80px;
        /* border: 1px solid red; */
    }
    #add-item-button{
        margin: 0 auto;
        padding-top: 10px;

        height: 70px;
        text-align: center;
        /* border: 1px solid black; */
        background-color:rgb(245, 245, 245);
    }
    .fix-button{
        position: fixed;
        width: 100%;
        padding-top: 5px;
        padding-bottom: 15px;
        bottom: 0px;
        text-align: center;
        /* border: 1px solid black; */
        /* margin: 0 auto; */
        background-color:rgb(245, 245, 245);
    }
    /* 设置对话框屏幕居中 */
    /* .el-dialog{
        display: flex;
        flex-direction: column;
        margin:0 !important;
        position:absolute;
        top:50%;
        left:50%;
        transform:translate(-50%,-50%);
        max-height:calc(100% - 30px);
        max-width:calc(100% - 30px);
    }
   .el-dialog .el-dialog__body{
        flex:1;
        overflow: auto;
    } */
</style>
    
<script>
export default {
    data() {
        var checkStitle = (rule, value, callback) => {
            if (value === '') {
                return callback(new Error('问卷标题不能为空'));
            }else{
                callback();
            }
        };
        var checkQtitle = (rule, value, callback) => {
            if (value === '') {
                return callback(new Error('问题内容不能为空'));
            }else{
                callback();
            }
        };
        var checkOption = (rule, value, callback) => {
            if (value === '') {
                return callback(new Error('选项内容不能为空'));
            }else{
                callback();
            }
        };
        return {
            uid: this.$route.query.id,
            sid: this.$route.query.sid,
            stitle: '',

            editStitleShow: false,
            editPublishShow: false,

            stitleForm:{
                stitle: ''
            },
            stitleRule:{
                stitle:[{required: true, validator: checkStitle, trigger: 'blur' }]
            }, 

            surveyForm: {
                question: [],
                delQues:[],//记录删除的问题编号列表
                delOps:[]//记录删除的选项编号列表
            },
            surveyRule:{
                question: []
            }                
        }
    },
    methods: {
        // 加载问卷标题
        // 加载问题列表
        loadQuesList(sid){
            this.axios.post("api/surveyPage",{sid:sid})
            .then(res => {
                if (res.status===200) {
                    this.surveyForm.question=res.data.questions;
                    this.surveyForm.question.forEach(element => {
                        if (element.qtype===3) {
                            // 为简答题添加表单验证
                            this.surveyRule.question.push({
                                qtitle:[{required:true,validator: this.checkQtitle, message:'问题内容不能为空', trigger: 'blur'}]
                            })
                        }else{
                            // 为选择题添加表单验证
                            this.surveyRule.question.push({
                                qtitle:[{required:true,validator: this.checkQtitle, message:'问题内容不能为空', trigger: 'blur'}],
                                options:[{
                                    option:[{validator: this.checkOption, message:'选项内容不能为空', trigger: 'blur'}]
                                },{
                                    option:[{validator: this.checkOption, message:'选项内容不能为空', trigger: 'blur'}]
                                }]
                            })
                        }
                    });
                    console.log(this.surveyForm.question)
                    this.stitle = res.data.stitle;
                    document.title = "编辑问卷—"+this.stitle;
                }
            })
            .catch(err => {
                console.error(err); 
            })
        },
        //保存中...的效果
        openFullScreen2() {
            const loading = this.$loading({
            lock: true,
            text: '正在保存该问卷...',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
            });
            setTimeout(() => {
                this.submitForm('surveyForm');
                loading.close();
            }, 1000);
        },
        //提交问题表单
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // 把问题表单中的数据传到后台
                    this.axios.post("api/editSurvey",{sid:this.sid,quesList:JSON.stringify(this.surveyForm)})
                    .then(res => {
                        console.log(res)
                        if (res.data.msg === "success") {
                            this.editPublishShow = true;
                        }else{
                            this.$message.error("出现错误，稍候再试。")
                        }
                    })
                    .catch(err => {
                        console.error(err);
                    })                 
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        handleSuccess(publish){
            this.axios.post("api/setPublish",{sid:this.sid,publish:publish})
            .then(res => {
                console.log(res)
                if(res.status===200){
                    if (res.data.msg === "success") {
                        this.editPublishShow = false;
                        this.$message({
                            type: "success",
                            message: "操作成功。"
                        });
                    }
                }
            })
            .catch(err => {
                console.error(err); 
            });
            return new Promise((resolve,reject) => {
                setTimeout(() => {
                    this.$router.push({
                        path: "/mysurvey",
                        name: "MySurvey",
                        query: {
                            id: this.uid
                        }
                    });
                }, 1000);
            })
        },
        // 设置修改问卷标题表单的默认值
        setStitleFormDefualt(){
            this.stitleForm.stitle = this.stitle;
            this.editStitleShow = true;
        },
        // 修改问卷标题操作
        handleEditStitle(formName){
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // 修改对应问卷的标题
                    this.axios.post('/api/setStitle',{
                        sid: this.sid,
                        stitle: this.stitleForm.stitle
                    })
                    .then(res => {
                        console.log(res)
                        if (res.status === 200) {
                            if (res.data.msg === 'success') {
                                this.stitle = res.data.stitle;
                                this.editStitleShow = false;
                            }else{
                                this.$message({
                                    message: "出现错误，请稍后再试。",
                                    type: 'warning'
                                })
                            }
                        }
                    })
                    .catch(err => {
                        console.error(err); 
                    })
                }
            })            
        },
        // 处理新增问题命令
        handleCommand(command) {
            if (command === 'one') {
                this.addQuestion1();
            }else if (command === 'more') {
                this.addQuestion2();
            }else if (command === 'text') {
                this.addQuestion3();
            }
        },
        // 增加问题选项
        addOption(options){
            options.push({
                option: ''
            });            
        },
        // 删除问题选项
        deleteOption(options,i,op){
            //先判断选项数量
            if (options.length>2) {
                //判断该选项是否有编号，有则在delOps[]中记录
                if (op.oid!=undefined) {
                    // console.log(op.oid)
                    this.surveyForm.delOps.push(op.oid);
                }
                options.splice(i,1);//在选项数组中移除
            }else{
                this.$message({
                    type:'warning',
                    message:'选择题必须至少有两个选项哦！'
                });
            }
                
        },
        // 删除该问题
        deleteQuestion(index,item){
            //判断该问题是否有编号，有则在delQues[]中记录
            if (item.qid!=undefined) {
                // console.log({qid:item.qid,qtype:item.qtype})
                this.surveyForm.delQues.push({qid:item.qid,qtype:item.qtype});                
            }
            this.surveyForm.question.splice(index,1);//在问题数组中移除
        },
        // 新增单选题
        addQuestion1(){
            // console.log('添加单选题')
            this.surveyForm.question.push({
                qtype: '1',
                qtitle: '',
                options: [{
                    option: ''
                },{
                    option: ''
                }]
            })
            // console.log('添加单选题表单验证')
            this.surveyRule.question.push({
                qtitle:[{required:true,validator: this.checkQtitle, message:'问题内容不能为空', trigger: 'blur'}],
                options:[{
                    option:[{validator: this.checkOption, message:'选项内容不能为空', trigger: 'blur'}]
                },{
                    option:[{validator: this.checkOption, message:'选项内容不能为空', trigger: 'blur'}]
                }]
            })
        },
        // 新增多选题
        addQuestion2(){
            // console.log('添加多选题')
            this.surveyForm.question.push({
                qtype: '2',
                qtitle: '',
                options: [{
                    option: ''
                },{
                    option: ''
                }]
            })
            // console.log('添加多选题表单验证')
            this.surveyRule.question.push({
                qtitle:[{required:true,validator: this.checkQtitle, message:'问题内容不能为空', trigger: 'blur'}],
                options:[{
                    option:[{validator: this.checkOption, message:'选项内容不能为空', trigger: 'blur'}]
                },{
                    option:[{validator: this.checkOption, message:'选项内容不能为空', trigger: 'blur'}]
                }]
            })
        },
        // 新增简答题
        addQuestion3(){
            // console.log('添加简答题')
            this.surveyForm.question.push({
                qtype: '3',
                qtitle: ''
            })
            // console.log('添加简答题表单验证')
            this.surveyRule.question.push({
                qtitle:[{required:true,validator: this.checkQtitle, message:'问题内容不能为空', trigger: 'blur'}]
            })
        }
    },
    mounted(){
        this.loadQuesList(this.sid);//加载该问卷的问题列表
    }
}
</script>




