<template>
    <div style="background-color:rgb(245, 245, 245);height:565px;">        
        <div style="width:100%;height:50px;"></div>    
        <div id="update-div">
            <h2 id="title-h3">修改账号密码</h2>
            <el-card  id="update">
                <el-form  id="pwsdForm" ref="pwsdForm" :model="pwsdForm" :rules="rules">
                    <el-form-item  prop="beforePwsd">
                        <el-input class="input-bgcolor" type="password" v-model="pwsdForm.beforePwsd" autocomplete="off" placeholder="请输入原来的密码" prefix-icon="my-icon" show-password></el-input>
                    </el-form-item>
                    <el-form-item  prop="newPwsd">
                        <el-input class="input-bgcolor" type="password" v-model="pwsdForm.newPwsd" autocomplete="off" placeholder="请输入新的密码" prefix-icon="my-icon" ></el-input>
                    </el-form-item>
                    <el-form-item  prop="checkPass">
                        <el-input class="input-bgcolor" type="password" v-model="pwsdForm.checkPass" autocomplete="off" placeholder="请再次输入新密码" prefix-icon="my-icon" ></el-input>
                    </el-form-item> 
                    <el-form-item>
                        <el-button style="width:100%;" type="warning" @click="submitForm('pwsdForm')" round>提交</el-button>
                    </el-form-item>
                </el-form>
            </el-card>
        </div>
    </div>
</template>

<style>
    #update-div {
        margin: 0 auto;

        width: 410px;
        height: 380px;
    }
    #title-h3{
        margin: 0 auto;
        padding-bottom:20px;
        height: 10%;

        color: #E6A23C;
    }
    #update{
        width: 100%;
        height: 80%;

        background: rgba(245, 245, 245, 0.8);
        /* border: ghostwhite; */
    }
    #pwsdForm {
        margin: 0 auto;
        padding: 20px;
        width: 280px;
    }
    .my-icon::after{
        width: 15px;

        content: '*';
        color: red;
    }
</style>

<script>
export default {
    data() {
        var validateOldPass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入原来的密码'));
            }
            else {
                if (this.pwsdForm.checkPass !== '') {
                    this.$refs.pwsdForm.validateField('checkPass');
                }
                callback();
            }
        };
        var validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入密码'));
            }
            else if (value.length<6 || value.length>12) {
                callback(new Error('密码长度在6至12个字符内'));
            }
            else {
                if (this.pwsdForm.checkPass !== '') {
                    this.$refs.pwsdForm.validateField('checkPass');
                }
                callback();
            }
        };
        var validatePass2 = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请再次输入密码'));
            } else if (value !== this.pwsdForm.newPwsd) {
                callback(new Error('两次输入密码不一致!'));
            } else {
                callback();
            }
        };
        return {
            uid: this.$route.query.id,
            pwsdForm: {
                beforePwsd: '',
                newPwsd: '',
                checkPass: ''
            },
            rules: {
                beforePwsd: [
                    {required: true, validator: validateOldPass, trigger: 'blur' }
                ],
                newPwsd: [
                    {required: true, validator: validatePass, trigger: 'blur' }
                ],
                checkPass: [
                    {required: true, validator: validatePass2, trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.axios
                    .post('/api/setPass',{
                        uid: this.uid,
                        oldPass: this.pwsdForm.beforePwsd,
                        newPass: this.pwsdForm.newPwsd
                    })
                    .then(res => {
                        if (res.status === 200) {
                            if (res.data.msg === 'fail') {
                                this.$message.error("出现错误，修改密码失败,请稍后重试。");
                            }else if (res.data.msg === 'success') {
                                this.toLogin();
                            }else if (res.data.msg === 'user-err') {
                                //不能存在该id
                                this.$message.error("当前账号登录状态有误，请稍后重试。");
                            } else if (res.data.msg === 'oldPass-err') {
                                //原本的密码输入有误
                                this.$message.error("密码错误，请输入当前账号的密码。");
                            }                     
                        }
                    })
                    .catch(err => {
                        this.$message(err);
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        toLogin(){
            //修改密码成功
            this.$message({
				message: "操作成功，请重新登录。",
				type: "success"
		    });
            //跳转至登录页面
            return new Promise((resolve,reject) => {
			  setTimeout(() => {
				  this.$router.push({
                      path: "/login",
                      name: "Login"
                  });
			  }, 2000);
		    })
        }
    },
    mounted(){        
        document.title = "修改账号密码";
    }
}
</script>




