import Vue from 'vue'
import Router from 'vue-router'
import Register from '@/pages/register'
import Login from '@/pages/login'
import Home from '@/pages/home'
import Me from '@/pages/me'
import UserInfo from '@/pages/userInfo'
import MySurvey from '@/pages/mysurveylist'
import Main from '@/pages/main'
import Survey from '@/pages/survey'
import Analysis from '@/pages/analysis'
import Demo from '@/pages/demo'
import Test from '@/pages/test'
import ResetPwsd from '@/pages/resetPwsd'
import UpdatePwsd from '@/pages/changePwsd'
import AllSurvey from '@/pages/allSurvey'
import NewSurvey from '@/pages/createSurvey'
import EditSurvey from '@/pages/editSurvey'
import ViewSurvey from '@/pages/viewSurvey'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/surveylist',
      name: 'AllSurvey',
      component: AllSurvey
    },
    {
      path: '/demo',
      name: 'Demo',
      component: Demo
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
    },
    {
      path: '/resetPwsd',
      name: 'ResetPwsd',
      component: ResetPwsd
    },
    {
      path: '/main',
      name: 'Main',
      component: Main,
      children: [
        {
            path: '/new-survey',
            name: 'NewSurvey',
            component: NewSurvey
        },
        {
            path: '/edit-survey',
            name: 'EditSurvey',
            component: EditSurvey
        },
        {
            path: '/me',
            name: 'Me',
            component: Me,
        },
        {
            path: '/mysurvey',
            name: 'MySurvey',
            component: MySurvey
        },
        {
            path: '/userinfo',
            name: 'UserInfo',
            component: UserInfo
        },
        {
            path: '/survey-data',
            name: 'SurveyData',
            component: Analysis
        },
        {
            path: '/updatePwsd',
            name: 'UpdatePwsd',
            component: UpdatePwsd
        },
        {
            path: '/view-survey',
            name: 'ViewSurvey',
            component: ViewSurvey,
        },
      ]
    },
    {
      path: '/survey',
      name: 'Survey',
      component: Survey
    },
    {
      path: '/test',
      name: 'Test',
      component: Test
    }
  ]
})



