<template>
    <div style="background-color:rgb(245, 245, 245);padding-bottom:45px;">        
        <!-- 回到顶部 -->
        <template>
            <el-backtop :bottom="100"></el-backtop>
        </template>
        <div class="title">
            <h2>{{stitle}}</h2>            
        </div>
        <div class="temp1"></div>
        <div>            
            <el-card class="context" shadow="always">
                <div class="survey-form">
                    <el-form id="surveyData" ref="surveyData"  :model="surveyData">
                        <div v-if="questions.length===0">
                            <h2>该问卷暂无问题</h2>
                        </div>
                        <el-form-item v-for="(question,i) in questions" :key="i" :prop="'res['+i+'].answer'">
                            <div class="qtitle-css">{{i+1}}、{{question.qtitle}}</div>
                            <!-- 单选题 -->
                            <div v-if="question.qtype===1 && question.qid===surveyData.res[i].qid" style="width:80%;">
                                <span v-for="(op,index) in question.options" :key="index">
                                    <el-radio v-model="surveyData.res[i].answer" :label="op.oid">{{op.option}}&nbsp;&nbsp;&nbsp;</el-radio>
                                </span>
                            </div>
                            <!-- 多选题 -->
                            <div v-if="question.qtype===2 && question.qid===surveyData.res[i].qid" style="width:80%;">
                                <el-checkbox-group v-model="surveyData.res[i].answer">
                                    <span v-for="op in question.options" :key="op" style="width:80%;">
                                        <el-checkbox :label="op.oid">{{op.option}}&nbsp;&nbsp;&nbsp;</el-checkbox>
                                    </span>
                                </el-checkbox-group>
                            </div>
                            <!-- 简答题 -->
                            <div v-if="question.qtype===3 && question.qid===surveyData.res[i].qid">
                                <el-input type="textarea" v-model="surveyData.res[i].answer" show-word-limit style="width:400px;" placeholder="请输入"></el-input>
                            </div>
                        </el-form-item>                     
                        <el-form-item>
                            <el-row style="color:#E6A23C;float:right;">
                                <h2>问卷预览</h2>
                            </el-row>
                        </el-form-item>
                    </el-form>
                </div>
            </el-card>
        </div>
    </div>
</template>

<style>
    body,html{
        background:rgb(245, 245, 245) !important;
    }
    
    #main1-menu {
        background-color: rgba(255, 255, 255, 0);
        border-bottom: none;
        height: 70px;
    }
    .temp{
        width: 100%;
        height: 50px;
    }
    .temp1{
        width: 100%;
        height: 25px;        
    }
    .border{
        border: 1px solid red;
    }
    .title{
        margin: 0 auto;
        margin-top: 30px;
        width: 980px;

        color: #E6A23C;
        text-align: center;
    }
    .context{
        margin: 0 auto;
        padding-top: 10px;
        padding-left: 50px;
        /* padding: 10px 50px; */

        width: 784px;
        /* height: 600px; */

        background: rgba(255, 255, 255, 0.5);     
    }
    .qtitle-css{
        color: #E6A23C;
        font-size: 16px;
    }
    .survey-form{
        margin: 0 auto;
        width: 730px;
        /* height: 400px; */
        font-size: 16px;   
    }    
    .confirmButton{
        background-color: #E6A23C !important;
        border-color: #E6A23C !important;
        font-size: 14px;
    }
    .cancelButton{
        font-size: 14px;
    }
    

</style>

<script>
export default {
    data(){     
        var checkAnswer = (rule, value, callback) => {
            if (value === '') {
                return callback(new Error('这一题还没有填哦！'));
            }else{
                callback();
            }
        };   
        return {
            sid: this.$route.query.sid,
            form:{
                options:''
            },
            surveyData:{
                sid: '',
                res:[]
            },
            rules:{
                res:[]
            },
            stitle: '',
            questions: []
        }
    },
    methods:{
        //加载问卷内容
        loadSurveyInfo(){
            console.log(this.sid)
            if(this.sid > 0){
                this.axios.post('/api/surveyPage',{
                    sid: this.sid
                 })
                .then(res => {
                    console.log(res)
                    if (res.status===200) {
                        this.stitle = res.data.stitle;                        
                        document.title = '预览—'+this.stitle;
                        this.surveyData.sid = this.sid;
                        this.questions = res.data.questions;
                        this.questions.forEach(element => {
                            if (element.qtype === 2) {
                                //多选题，添加的回答数据结构为数组
                                this.surveyData.res.push({
                                    qid:element.qid,
                                    qtype: element.qtype,
                                    answer:[]
                                });
                            }else{
                                this.surveyData.res.push({
                                    qid:element.qid,
                                    qtype: element.qtype,
                                    answer:''
                                });
                            } 
                            this.rules.res.push({
                                answer:[{required: true, validator: this.checkAnswer,message:"这一题还没有填哦！", trigger: 'blur'}]
                            });    
                        });
                        console.log(this.surveyData.res)
                    }
                })
                .catch(err => {
                    console.error(err); 
                })
            }
        }
    },
    mounted(){
        this.loadSurveyInfo();
    }
}
</script>


