<template>
    <div style="height:600px;">
        <el-header>
            <el-menu  id="main-menu"  class="el-menu-demo" mode="horizontal"
                active-text-color="#E6A23C">
                <router-link to="/">
                    <el-col :span="8" class="text1-color">
                        <h1>问卷中心</h1>                  
                    </el-col>       
                </router-link>           
                <div style="float:right;padding:18px 50px;">
                    <router-link to="/register">
                        <el-button round>注册</el-button>
                    </router-link>
                    <router-link to="/login">
                        <el-button  round>登录</el-button>
                    </router-link>                                       
                </div>
            </el-menu>
        </el-header>
        <div style="width:100%;height:50px;"></div>        
        <div class="reset-title">重置密码</div>
        <div id="reset">
            <el-card class="ecard-css">
                <el-form  id="resetPwsdForm"   ref="resetPwsdForm" :model="resetPwsdForm" :rules="rules" >
                    <el-form-item  prop="username">
                        <el-input class="input-bgcolor" v-model="resetPwsdForm.username" placeholder="请输入您的用户名" prefix-icon="my-icon"></el-input>
                    </el-form-item>
                    <el-form-item  prop="telephone">
                        <el-input class="input-bgcolor"  v-model="resetPwsdForm.telephone" placeholder="请输入您的手机号" prefix-icon="my-icon"></el-input>
                    </el-form-item>
                    <el-form-item  prop="password">
                        <el-input class="input-bgcolor" type="password" v-model="resetPwsdForm.password" autocomplete="off" placeholder="请输入新的密码" prefix-icon="my-icon"></el-input>
                    </el-form-item>
                    <el-form-item  prop="checkPass">
                        <el-input class="input-bgcolor" type="password" v-model="resetPwsdForm.checkPass" autocomplete="off" placeholder="请您确认密码" prefix-icon="my-icon"></el-input>
                    </el-form-item> 
                    <el-form-item>
                        <el-button style="width:100%;" type="warning" @click="submitForm('resetPwsdForm')" round>提交</el-button>
                    </el-form-item>
                </el-form>
            </el-card>
        </div>
    </div>
</template>

<style>
    #main-menu {
        background-color: rgba(255, 255, 255, 0);
        border-bottom: none;
        height: 70px;
    }
    .text1-color{
        color: #E6A23C;
        text-align: center;
        line-height: 61px;
        font-size: 12px;
    }
    #reset{
        margin: 0 auto;
        /* margin-top: 200px; */

        width: 410px;
        /* height: 300px; */

        background-color:rgb(245, 245, 245);
        text-align: center;
        /* border: 1px solid red; */
        /* border-radius: 25px; */
    }
    .ecard-css{
        background-color: rgba(255, 255, 255, 0.5);
    }

    .reset-title{
        margin: 0 auto;
        padding-bottom:20px;

        width: 410px;

        font-size: 24px;
        font-weight: 300;        
        text-align: center;
    }

    #resetPwsdForm{
        margin: 0 auto;
        /* padding: 20px 30px 20px 5px; */

        width: 280px;
    }

    .my-icon::after{
        width: 15px;

        content: '*';
        color: red;
    }    

</style>

<script>
export default {
    data() {
        var checkUsername = (rules,value, callback) => {
            if (value === '') {
                return callback(new Error('请输入用户名'));
            }else{
                callback();
            }
        };
        var checkTelephone = (rule, value, callback) => {
            if (value === '') {
                return callback(new Error('请输入手机号'));
            }else if (value.length != 11) {
                return callback(new Error('请输入正确的手机号'));
            }else{
                callback();
            }
        };
        var validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入密码'));
            }
            else if (value.length<6 || value.length>12) {
                callback(new Error('密码长度在6至12个字符内'));
            }
            else {
                if (this.resetPwsdForm.checkPass !== '') {
                    this.$refs.resetPwsdForm.validateField('checkPass');
                }
                callback();
            }
        };
        var validatePass2 = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请再次输入密码'));
            } else if (value !== this.resetPwsdForm.password) {
                callback(new Error('两次输入密码不一致!'));
            } else {
                callback();
            }
        };
        return {
            resetPwsdForm: {
                username: '',
                telephone: '',
                password: '',
                checkPass: ''
            },
            rules: {
                username: [
                    {required: true, validator: checkUsername, trigger: 'blur' }
                ],
                telephone: [
                    {required: true, validator: checkTelephone, trigger: 'blur'}
                ],
                password: [
                    {required: true, validator: validatePass, trigger: 'blur' }
                ],
                checkPass: [
                    {required: true, validator: validatePass2, trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.axios
                    .post('/api/resetPass',JSON.stringify(this.resetPwsdForm))
                    .then(res => {
                        if (res.status === 200) {
                            if (res.data.msg === 'fail') {
                                this.$message.error("出现错误，重置密码失败,请稍后重试。");
                            }else if (res.data.msg === 'success') {
                                this.toLogin();
                            }else if (res.data.msg === 'name-err') {
                                //用户名输入有误
                                this.$message.error("不存在此用户名的账号，请输入正确的账号信息。");
                            } else if (res.data.msg === 'phone-err') {
                                //手机号输入有误
                                this.$message.error("手机号信息错误，请输入正确的账号信息。");
                            }                     
                        }
                    })
                    .catch(err => {
                        this.$message(err);
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        toLogin(){
            //重置密码成功
            this.$message({
				message: "操作成功，正在前往登录页面...",
				type: "success"
		    });
            //跳转至登录页面
            return new Promise((resolve,reject) => {
			  setTimeout(() => {
				  this.$router.push({
                      path: "/login",
                      name: "Login"
                  });
			  }, 2000);
		    })
        }
    },
    mounted(){
        document.title = "忘记密码——重置";   
    }
}
</script>


